@media (max-width: 768px) {

    .show__content__elem {
        width: 60vh;
        min-width: 400px;
        min-height: 215px;
    }

    .show__content_btn-remove{
        padding: 20px;
    }

}
@media (max-width: 425px) {

    .show__content__elem {
        width: 35vh;    
        max-width: 35%; 
        margin: 0 auto;   
    }

    .show__content_btn-remove{
        display: none;
    }

}
