@import url(https://fonts.googleapis.com/css2?family=Inter&family=Open+Sans&family=Raleway:wght@500&display=swap);

/* font-family: 'Inter', sans-serif;
font-family: 'Open Sans', sans-serif;
font-family: 'Raleway', sans-serif; */

@-webkit-keyframes opacity {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes opacity {
    from { opacity: 0; }
    to { opacity: 1; }
}

@-webkit-keyframes timeout {
    from { opacity: 1; }
    to { opacity: 0; }
}

@keyframes timeout {
    from { opacity: 1; }
    to { opacity: 0; }
}

@-webkit-keyframes toleft {
    from { -webkit-transform: translateX(100%); transform: translateX(100%) }
    to { -webkit-transform: translateX(0%); transform: translateX(0%) }
}

@keyframes toleft {
    from { -webkit-transform: translateX(100%); transform: translateX(100%) }
    to { -webkit-transform: translateX(0%); transform: translateX(0%) }
}

@-webkit-keyframes toright {
    from { -webkit-transform: translateX(0); transform: translateX(0) }
    to { -webkit-transform: translateX(100%); transform: translateX(100%) }
}

@keyframes toright {
    from { -webkit-transform: translateX(0); transform: translateX(0) }
    to { -webkit-transform: translateX(100%); transform: translateX(100%) }
}
        /* General propertis */

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html, body {
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
}

.header, .main, .footer {
    background-color: #F6FCF4;   
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;
}


.container {
    width: 90%;
    max-width: 1280px;
    margin: 0 auto;
}

h3 {
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 29px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #1F2E19;
    margin-bottom: 75px;
}

h4 {
    font-family: 'Raleway', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 26px;
    text-transform: uppercase;
    vertical-align:middle;
    color: #1F2E19;
    margin-bottom: 40px;
}

h4  > span {
    font-family: 'Open Sans', sans-serif;
}

p {
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: #1F2E19
}

li {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #1F2E19;
    margin-left: 20px;
}


        /* Forms to apply */

.prices__list__item {
    height: 290px;
    width: 32%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background: rgba(31, 46, 25, 0.85);
    border-radius: 30px;
    margin: 0;
    padding: 50px 70px;
}


.item_title {
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 26px;
    color: #FFFAFA;
    margin-bottom: 0;
}

.item_time {
    font-weight: 600;
    font-size: 20px;
    line-height: 26px;
    color: #7BC361;
}

.item_price {
    font-weight: 600;
    font-size: 20px;
    line-height: 26px;
    color: #FFFAFA;
}

.item__btn {
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    color: rgba(31, 46, 25, 0.85);
    background: #FFFAFA;
    border: 1px solid #FFFAFA;
    border-radius: 10px;
    padding: 10px 50px;
    cursor: pointer;
    margin-top: 10px;
}

/* Top signs on the pages of school and nursery */


.page__offeres {
    display: block;
    width: 160px;
    height: 190px;
    background: #FFFAFA;
    border: 1px solid #1F2E19;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    margin-left: 0;
    padding: 10px 15px;
}


.offer_about {
    display: block;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: #1F2E19;
}

.offer_picture {
    width: 90px;
    height: 90px;
    background-color: #C8E7BD;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}

        /* Photos on the pages of school and nursery */

.content_photo {
    
    position: relative;
    width: 540px;
    height: 350px;
    border-radius: 30px; 
}

.content_photo > img {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    
}

        /* Header */

.header .container {
    position: fixed;
    top: 0;
    left: 50%;
    -webkit-transform: translate(-50%,0);
            transform: translate(-50%,0);
    z-index: 20;
}

.header__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Inter', sans-serif;
    font-size: 15px;
    line-height: 18px;
    background-color: #F6FCF4; 
    height: 80px;
    padding: 0 25px;
}

.header__logo > img {
    display: block;
}

.nav {
    margin-left: auto;
    margin-right: 30px;
}

.header__phone {
    margin: 0 30px;
}

.header__btn, .form__btn, .review__btn {
    color: #F6FCF4;
    padding: 10px 45px;
    margin-left: 30px;
    background: #1F2E19;
    border: 1px solid #FFFAFA;
    border-radius: 10px;
    cursor: pointer;
}


.nav a, .header__phone a {
    color: #1F2E19;
    text-decoration: none;
    padding: 5px;
}

.nav a:hover, .header__phone a:hover, .header__btn:hover, .header__form__btn:hover, .form__btn:hover, .review__btn:hover {
    color: #7BC461;
}


.nav > ul {
    display: flex;
    justify-content: center;
    grid-gap: 15px;
    gap: 15px;
}

.nav li {
    position: relative;
}

.nav li > ul {
    display: none;
}

.nav li.hover > ul {
    display: block;
    position: absolute;
    white-space: nowrap;
    background-color: #FFFF;
    border: none;
    border-radius: 3px;
    padding: 10px;
}

.nav li > ul a {
    display: block;
    padding: 10px;
}

.nav li {
    list-style: none;
}

.nav__link.active {
    text-decoration: underline;
    color: #7BC461; 
} 

